@font-face {
  font-family: "GangwonEdu_OTFBoldA";
  src: url("https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2201-2@1.0/GangwonEdu_OTFBoldA.woff")
    format("woff");
  font-weight: normal;
  font-style: normal;
}

* {
  font-family: "GangwonEdu_OTFBoldA", sans-serif;
  /* sans-serif는 대체 폰트로 지정됩니다. */
}

.blinking-cursor {
  user-select: none;
  animation: blink 1s steps(2, start) infinite;
}

@keyframes blink {
  to {
    visibility: hidden;
  }
}

.type-async {
  color: white;
  font-size: 2rem;
}

.informationIcon {
  width: 2rem;
  height: 2rem;
}

.mTitle {
  color: black;
}

.mTitle {
  /* display: flex; */
  flex-direction: column;
  align-items: start;
}

.locationEmail {
  display: flex;
  align-items: center;
  margin-top: 10px;
}

.locationEmail img {
  margin-right: 10px;
}

.locationEmail a {
  color: #000000;
  /* 예시로 파란색을 사용 */
  text-decoration: none;
}

.locationEmail a:hover {
  text-decoration: underline;
}

.projectTitle {
  text-align: center;
}
.BlogTitle {
  text-align: center;
}
/* ============== */
body {
  background: linear-gradient(to right, #43507e, #5f486f);
  perspective: 900px;
  margin: 0;
}

.mainTitle {
  align-items: baseline;
}
.title {
  font-size: 1.5rem;
}

/* ====Button css===== */
.app-button {
  background-color: #f5f5f5;
  border-radius: 20px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  padding: 10px;
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
}

.app-button img {
  border-radius: 15px;
  transition: all 0.3s ease;
}

.app-button:hover img {
  transform: scale(1.05);
}
